@import "../../static/css/index.scss";

.ly {
  width: 100%;
  height: 100%;
  .l_header {
    width: 100%;
    height: 1.6rem;
    background: #c1b696;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: px2rem(18);
    color: #fff;
    position: fixed;
    top: 0;
    left: 0;
    span:nth-child(1) {
      height: 100%;
      // background: red;
      display: flex;
      align-items: center;
      margin-left: px2rem(-8);
    }
    .left {
      margin-left: px2rem(18);
      color: #fff;
    }
    .addLy {
      font-size: px2rem(14);
      margin-right: px2rem(18);
    }
  }
  .ly_content {
    height: 100%;
    margin-top: 1.6rem;
    position: relative;
    overflow: hidden;
    z-index: 1;
  }
  .l_wrapper {
    z-index: 1;
    .ly_contentBox {
      .word {
        width: 100%;
        min-height: px2rem(130);
        // background: red;
        border-bottom: 1px solid #e6e4e4;
        box-sizing: border-box;
        padding: 0 px2rem(12);
        text-align: justify;
        font-size: px2rem(14);
        display: flex;
        flex-direction: column;
        justify-content: center;
        color: #424040;
        // align-items: center;
        .time {
          margin-bottom: 8px;
          span:nth-child(1) {
            margin-right: 6px;
          }
        }
        .user {
          margin-bottom: 6px;
        }
      }
    }
  }

  .addLy_box {
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(64, 59, 59, 0.5);
    left: 0;
    right: 0;
    z-index: 998;
    display: flex;
    justify-content: center;
    .ly_input {
      width: px2rem(340);
      height: px2rem(340);
      background: #fff;
      margin-top: px2rem(80);
      border-radius: 8px;
      //   display: flex;
      //   flex-direction: column;
      //   justify-content: center;
      align-items: center;
      .am-list-item {
        // padding-left: 0 !important;
        border-radius: 8px;

        border-bottom: 1px solid #f1efea;
      }

      .am-textarea-control {
        color: #646464 !important;
        // width: px2rem(340);
        //border: 1px solid red;
      }
      .am-textarea-control textarea {
        color: #646464 !important;
      }
    }

    .action {
      display: flex;
      justify-content: flex-end;
      margin-top: 18px;
      .am-button {
        line-height: 30px !important;
        font-size: px2rem(14);
      }
      .am-button-primary {
        background: #cec4ac;
      }
      .am-button-primary::before {
        border: none;
      }
    }
  }
}
